<script>
import axios from "axios"
export default {
    data() {
        return {
            fb: [],
            scheme: [],
            other: [],
            timer: 0,
            totalDay: [{}, {}],
            totalMounth: [{}, {}],
            showDailyInfo: true,
            isLoading: false,
            error: ""
        }
    },
    methods: {
        switchTotalInfo() {
            let switchTimer = setTimeout(()=>{
                this.showDailyInfo = !this.showDailyInfo
                clearTimeout(switchTimer)
                this.switchTotalInfo()
            }, 300500)
        },
        setData(obj1, obj2, key) {
            let result = []
            if (Array.isArray(obj1[key])){
                obj1[key].forEach(el=>{
                    let copy = Object.assign({}, el)
                    let finding = obj2[key].find(el2=>{if (el.reckl === el2.reckl){ 
                            return el2
                            }
                        }
                    )
                    for (let [key, value] of Object.entries(finding)) {
                        copy[`${key}Month`] = value
                    }
                    result.push(copy)
                })
            }
            return result
        },
        calculatePercent(val1, val2) {
            return (Number(val1)/Number(val2))*100 <= 100 ? Math.round((Number(val1)/Number(val2))*100) : 100
        },
        loadData() {
            this.isLoading = true
            this.error = ""
            axios.get('http://185.92.151.44/Day_merj')
                .then(res=>{
                    let keys = ['FB', 'scheme', 'other']
                    for (let key of keys) {
                        this[key.toLowerCase()] = this.setData(res.data.Total_Day, res.data.TotalInformation_month, key)
                    }
                    return res.data
                })
                .then(data=>{
                    this.totalDay = [...data.Total_Day.Total]
                    this.totalMounth = [...data.TotalInformation_month.Total]
                })
                .catch(err=>{
                    console.log(err)
                    this.error = "При загрузке данных произошла ошибка. Перезагрузите страницу"
                })
                .finally(()=>{
                    this.isLoading = false
                })
        },
        updateData() {
            this.loadData()
            if (this.timer !== 0) {
                clearInterval(this.timer)
                this.timer = 0
            }
            this.timer = setInterval(()=>{
                this.updateData()
            }, 300000)         
        }
    },
    mounted() {
        this.updateData()
        this.switchTotalInfo()
    }
}
</script>

